    'roadmap#8': 'There will be a big update for the platform at this stage: users will be able to store and exchange cryptocurrencies and fiat. CINDX.trade is an extremely flexible system of order execution and risk management. The Vendorâ€™s market will appear; however, at this point, the Vendorâ€™s market will only contain applications developed by CINDX and its partners.  ',
    'roadmap#9': 'A tokenized rating system will be added in the final version of CINDX.talk, as well as a new type of product: â€œalgorithmic tradingâ€. We will also add an automatic arbitrage function that will operate between exchanges and multi-exchange orders.   ',
