'mediaNews#1_1':'CINDX Aims to Democratize the Modern Financial System','mediaNews#1_2':'Even as investors in the cryptocurrency space recognize that they are at the forefront of a new financial revolution, adoption by the general populace is yet to occur. While 3rd parties are slowly entering the space in the form of hedge funds and other investors, very few individuals seem to have taken the step to invest in cryptocurrencies on their own.','mediaNews#1_3':'CINDX Announces Plans For ICO Pre-Sale Amidst Release Of Revolutionary Crypto Trading Hub That Allows Users To Follow Expert Traders','mediaNews#1_4':'CINDX takes its business seriously with particular attention and cares to legal matters and has taken great pains to ensure that CINDX is as compliant as possible with global regulations.','mediaNews#1_5':'How CINDX is Democratizing the Modern Financial System','mediaNews#1_6':'CINDX is a new platform that is making crypto trading easier, more secure, and more accessible. With this platform, itâ€™s possible to have your funds traded without high fees or minimum investments, with the added security and transparency of blockchain technology.','mediaNews#1_7':'CINDX is making crypto trading easier, more secure, and more accessible.','mediaNews#1_8':'Cryptocurrency and blockchain technology have been slated as powerful tools capable of fueling a global financial revolution. There are myriad opportunities to invest in the crypto space, and yet, very few individuals have undertaken the steps necessary to invest.','mediaNews#1_9':'CINDX Announces Plans Release Of Revolutionary Crypto Trading Hub','mediaNews#1_10':'The CINDX platform creates a complete ecosystem for cryptocurrency trading that is favorable to all the participants of the market and offers solutions to existing pain points.','mediaNews#1_11':'CINDX Announces Plans For ICO Pre-Sale','mediaNews#1_12':'CINDX made headlines this week with the announcement that they will be opening their ICO Pre-Sale in late August after investors interest in the ICO peaks to new levels.','mediaNews#1_13':'CINDX Will Be Opening ICO Pre-Sale in late August','mediaNews#1_14':'The CINDX Crypto trading platform has a myriad of features and benefits that havenâ€™t been seen in other platforms to date.','mediaNews#1_15':'5 Tips from CINDX Crypto Assets Management Hub: How to Avoid Being Cheated','mediaNews#1_16':'The team behind the CINDX crypto assets management hub has created a helpful set of guidelines on how not to become victims of unfair play, so you can learn what should be primarily taken into account when evaluating ICOs before investing in them.','mediaNews#1_17':'CINDX Crypto Asset Management Hub Adopts Bancor Protocol','mediaNews#1_18':'CINDX is delighted to announce that a number of new integrations with top industry players are underway, each of which will make the life of the users of the platform easier and richer. These new integrations include the decision to adopt the Bancor protocol to provide continuous liquidity for the CINDX platform.',// News
